
.el-breadcrumb {
  margin-top: 50px;
  line-height: 22px;
}
.el-breadcrumb .el-breadcrumb__item__inner {
    font-family: PingFangSC-Light;
    font-size: 16px;
    font-weight: 300;
    color: #888888 !important;
}
.ad-app-list {
  width: 100%;
  padding-right: 35px;
  font-size: 0px;
}
.ad-app-list a {
    font-family: PingFangSC-Regular;
    font-size: 13px;
    color: #4a90e2;
}
.ad-app-list a.mrg-l18 {
      margin-left: 18px;
}
.ad-app-list .addAd-btn {
    border-color: #F5A623;
    border: none;
    height: 40px;
    vertical-align: top;
    color: #fff;
    background: #F5A623;
    width: 108px;
    padding: 10px;
    line-height: 14px;
    font-size: 12px;
    -webkit-transition: all 0.48s ease-out;
    -o-transition: all 0.48s ease-out;
    transition: all 0.48s ease-out;
}
.ad-app-list .addAd-btn:hover {
      background: rgba(245, 166, 35, 0.7);
      border-color: rgba(245, 166, 35, 0.7);
}
.ad-app-list .addAd-btn span {
      font-family: PingFangSC-Semibold !important;
}
.ad-app-list .addAd-btn .add-logo {
      position: relative;
      right: 5px;
}
.ad-app-list .table-wrapper {
    width: 100%;
    margin-top: 30px;
    margin-bottom: 20px;
}
.ad-app-list .table-wrapper .el-table .el-table__body-wrapper .not-default-space .cell {
      font-size: 0px;
}
.ad-app-list .table-wrapper .el-table .el-table__row td:first-child .cell {
      white-space: nowrap;
}
.ad-app-list .table-wrapper .cursor-point .cell {
      cursor: pointer;
}



