
.qk-dash-card {
  height: 92px;
  padding: 16px 20px;
  border: 1px dashed #DDDDDD;
}
.qk-dash-card .title, .qk-dash-card .highlight, .qk-dash-card .lowlight {
    font-family: PingFangSC-Regular;
    letter-spacing: 0;
}
.qk-dash-card .title, .qk-dash-card .highlight {
    margin-bottom: 8px;
}
.qk-dash-card .title {
    font-size: 14px;
    line-height: 20px;
    color: #3A3A3A;
}
.qk-dash-card .highlight {
    font-size: 22px;
    line-height: 30px;
    color: #4A90E2;
}
.qk-dash-card .lowlight {
    font-size: 12px;
    line-height: 17px;
    color: #888888;
}



