
.account-container {
  padding: 50px 0 150px 35px;
}
.account-container .el-form-item__content {
    font-size: 13px;
}
.account-container .el-form-item__label {
    font-size: 13px;
}
.account-container .c3a {
    color: #3A3A3A;
}
.account-container .phone-wrap {
    display: inline-block;
    width: 100%;
    vertical-align: bottom;
    line-height: 40px;
}
.account-container .phone-wrap .mrg-l31 {
      margin-left: 31px;
}
.account-container .phone-wrap .c42 {
      font-weight: 600;
      color: #4a90e2;
      text-decoration: underline;
}
.account-container .title {
    font-family: PingFangSC-Light;
    margin-top: 0px;
    font-size: 16px;
    line-height: 22px;
    color: #888888;
    letter-spacing: 0;
    margin-bottom: 36px;
}
.account-container .mrg-t80 {
    margin-top: 60px;
}
.account-container .mrg-t20 {
    margin-top: 20px;
}
.account-container .fs13-c3a {
    font-size: 13px;
    color: #3a3a3a;
}
.account-container .break-line {
    font-family: PingFangSC-Regular;
    position: relative;
    width: 100%;
    line-height: 40px;
    display: inline-block;
}



