
.addapply-container {
  width: 100%;
  padding: 50px 0 150px 35px;
}
.addapply-container .breadcrumb {
    height: 22px;
}
.addapply-container .breadcrumb .breadcrumb-item {
      display: inline-block;
      float: left;
      line-height: 22px;
      margin-right: 10px;
      cursor: pointer;
}
.addapply-container .breadcrumb .breadcrumb-item .breadcrumb-item-inner {
        font-family: PingFangSC-Light;
        font-size: 16px;
        line-height: 22px;
        color: #888888;
}
.addapply-container .breadcrumb .breadcrumb-item .breadcrumb-separator {
        width: 6px;
        height: 12px;
        margin-left: 5px;
        display: inline-block;
        background-image: url("//qianka.b0.upaiyun.com/images/a688c7dd7a765df07ec7d9cfab76b68f.png");
        background-size: 6px 12px;
        background-position: center;
        background-repeat: no-repeat;
}
.addapply-container .breadcrumb .breadcrumb-item:last-child {
      cursor: text;
}
.addapply-container .search-wrapper {
    margin: 47px 35px 30px 0;
    text-align: center;
    border-bottom: 1px solid #f5f5f5;
}
.addapply-container .app-wrapper {
    height: auto;
    overflow: hidden;
}
.addapply-container .app-wrapper .app-list {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      width: 50%;
      padding: 0 55px 30px 55px;
      float: left;
      border-right: 1px solid #f5f5f5;
}
.addapply-container .app-wrapper .app-list:nth-child(odd) {
        padding-left: 0;
}
.addapply-container .app-wrapper .app-list:nth-child(even) {
        border-right: 0 none;
}
.addapply-container .app-wrapper .app-list:last-child {
        padding-bottom: 0;
        margin-bottom: 30px;
}
.addapply-container .app-wrapper .app-list:nth-last-child(2) {
        padding-bottom: 0;
        margin-bottom: 30px;
}
.addapply-container .app-wrapper .app-list .image {
        width: 50px;
        border: solid 0.5px rgba(58, 58, 58, 0.2);
        height: 50px;
        overflow: hidden;
        border-radius: 6px;
        float: left;
        margin-right: 15px;
}
.addapply-container .app-wrapper .app-list .image img {
          width: 50px;
          height: 50px;
          border-radius: 5px;
}
.addapply-container .app-wrapper .app-list .title {
        -webkit-box-flex: 1;
        -webkit-flex: 1;
            -ms-flex: 1;
                flex: 1;
        height: 50px;
        padding-top: 6px;
        overflow: hidden;
        white-space: nowrap;
        -o-text-overflow: ellipsis;
           text-overflow: ellipsis;
}
.addapply-container .app-wrapper .app-list .title p {
          display: block;
          overflow: hidden;
          white-space: nowrap;
          -o-text-overflow: ellipsis;
             text-overflow: ellipsis;
          font-size: 14px;
          font-weight: 600;
          color: #3a3a3a;
          margin: 0;
          line-height: 18px;
}
.addapply-container .app-wrapper .app-list .title span {
          display: block;
          padding-top: 9px;
          font-size: 13px;
          margin: 0;
          color: #888888;
}
.addapply-container .app-wrapper .app-list .button {
        width: 150px;
}
.addapply-container .app-wrapper .app-list .button .hidden {
          display: none;
}
.addapply-container .app-wrapper .app-list .button p {
          line-height: 50px;
          font-size: 13px;
          float: right;
          margin: 0;
          color: #888888;
}
.addapply-container .app-wrapper .app-list .button .el-button {
          float: right;
          margin: 9px 0 9px 17px;
}
.addapply-container .hide:last-child {
    display: none;
}
.addapply-container .el-dialog {
    width: 390px;
    height: 152px;
    padding: 30px;
}
.addapply-container .el-dialog .el-dialog__header .el-dialog__title {
      line-height: 22px;
}
.addapply-container .el-dialog .el-dialog__header .el-dialog__headerbtn {
      z-index: 99;
      position: relative;
}
.addapply-container .el-dialog .el-dialog__header .el-dialog__headerbtn .el-dialog__close {
        color: #B5B5B5;
        font-size: 14px;
}
.addapply-container .el-dialog .el-dialog__body {
      padding: 0 0 0 45px;
}
.addapply-container .el-dialog .el-dialog__body .logo {
        top: 0px;
}
.addapply-container .el-dialog .el-dialog__body .qk-title {
        position: relative;
        top: 7px;
        font-size: 16px;
}
.addapply-container .el-dialog .dialog-footer {
      padding-top: 25px;
      position: relative;
      display: block;
      text-align: right;
}
.addapply-container .el-dialog .dialog-footer p {
        position: absolute;
        top: -17px;
        left: 116px;
        width: 40px;
        height: 24px;
        line-height: 24px;
        border-radius: 2px;
        color: #fff;
        background-color: #de4948;
}
.addapply-container .el-dialog .dialog-footer p:after {
          color: #de4948;
          position: absolute;
          top: 15px;
          left: 12px;
}
.addapply-container .el-dialog .dialog-footer img {
        position: absolute;
        top: 19px;
        left: 125px;
}
.addapply-container .el-loading-mask {
    top: 35px;
}



