
#page {
  width: 100%;
  height: 100%;
}
#page #page-content {
    width: 100%;
    height: 100%;
    padding-top: 200px;
    background: #3C4F64;
}
#page #page-content .form-wrapper {
      background: #3C4F64;
      position: absolute;
      width: 340px;
      top: 50%;
      left: 50%;
      margin-top: 50%;
      margin-left: 50%;
}
#page #page-content .form-wrapper.form-wrapper-register {
        height: 530px;
        margin: -265px 0 0 -170px;
}
#page #page-content .form-wrapper .el-row .el-col .grid-content {
        max-width: 340px;
        margin: 0 auto;
        margin-bottom: 30px;
        padding: 35px;
        background: #fff;
        text-align: center;
}
#page #page-content .form-wrapper .el-row .el-col .grid-content .register-form {
          margin-top: 23px;
}
#page #page-content .form-wrapper .el-row .el-col .grid-content .register-form .qk-form-item {
            width: 100%;
}
#page #page-content .form-wrapper .el-row .el-col .grid-content .register-form .btn-register {
            width: 100%;
            height: 42px;
            margin-top: 13px;
}
#page #page-content .form-wrapper .el-row .el-col .grid-content .desc {
          font-family: PingFangSC-Regular;
          font-size: 12px;
          color: #B5B5B5;
          margin-top: 32px;
          margin-bottom: 1px;
          text-align: center;
}
#page #page-content .form-wrapper .el-row .el-col .grid-content .desc a {
            color: #4A90E2;
}
#page #page-content .form-wrapper .el-row .el-col .copyright {
        text-align: center;
        opacity: 0.2;
        font-family: Lato-Bold;
        font-size: 12px;
        color: #FFFFFF;
}
#page .el-dialog {
    padding: 30px;
}
#page .el-dialog .el-dialog__body {
      padding: 40px 0 19px 45px;
}



