
.ad-container {
  width: 100%;
  min-width: 1000px;
  height: 100%;
  padding: 43px 35px 150px 35px;
  position: relative;
}
.ad-container .unbind-tel-wrap {
    height: 42px;
    line-height: 18px;
    font-size: 13px;
    color: #e79814;
    margin: 5px 0 0 0;
    padding: 12px 18px;
    background: #fff9ef;
    /*opacity: 0.06;*/
    /*background-color: #f5a623;*/
}
.ad-container .unbind-tel-wrap .c42 {
      font-weight: 600;
      color: #4a90e2;
      text-decoration: underline;
}
.ad-container .unbind-tel-wrap img.icon {
      vertical-align: top;
      margin-right: 8px;
}
.ad-container .breadcrumb {
    height: 22px;
    margin-top: 7px;
    margin-bottom: 47px;
}
.ad-container .breadcrumb .breadcrumb-item {
      display: inline-block;
      float: left;
      margin-right: 10px;
      cursor: pointer;
}
.ad-container .breadcrumb .breadcrumb-item .breadcrumb-item-inner {
        font-family: PingFangSC-Light;
        font-size: 16px;
        line-height: 22px;
        color: #888888;
}
.ad-container .breadcrumb .breadcrumb-item .breadcrumb-separator {
        width: 6px;
        height: 12px;
        margin-left: 2px;
        display: inline-block;
        background-image: url("//qianka.b0.upaiyun.com/images/a688c7dd7a765df07ec7d9cfab76b68f.png");
        background-size: 6px 12px;
        background-position: center;
        background-repeat: no-repeat;
}
.ad-container .breadcrumb .breadcrumb-item:last-child {
      cursor: text;
}
.ad-container .title {
    font-family: PingFangSC-Light;
    font-size: 16px;
    color: #888888;
    margin-top: 7px;
    margin-bottom: 47px;
}
.ad-container .title .i.el-icon-arrow-right {
      font-size: 12px;
      margin: 0 5px 0 5px;
}
.ad-container .search-wrapper {
    font-size: 0;
    margin-bottom: 30px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.ad-container .search-wrapper .mrg-l10 {
      margin-left: 10px;
}
.ad-container .search-wrapper .input-wrapper {
      display: inline-block;
      float: left;
}
.ad-container .search-wrapper .input-wrapper .el-select, .ad-container .search-wrapper .input-wrapper .el-form, .ad-container .search-wrapper .input-wrapper .el-button {
        display: inline-block;
}
.ad-container .search-wrapper .addAd-btn {
      border-color: #F5A623;
      border: none;
      height: 40px;
      vertical-align: top;
      color: #fff;
      background: #F5A623;
      width: 121px;
      padding: 10px;
      line-height: 14px;
      font-size: 12px;
      -webkit-transition: all 0.48s ease-out;
      -o-transition: all 0.48s ease-out;
      transition: all 0.48s ease-out;
}
.ad-container .search-wrapper .addAd-btn:hover {
        background: rgba(245, 166, 35, 0.7);
        border-color: rgba(245, 166, 35, 0.7);
}
.ad-container .search-wrapper .addAd-btn span {
        font-family: PingFangSC-Semibold !important;
}
.ad-container .search-wrapper .addAd-btn .add-logo {
        position: relative;
        right: 5px;
}
.ad-container .el-date-range-picker {
    z-index: 10001 !important;
}
.ad-container .table-wrapper {
    width: 100%;
    margin: 20px 0px;
}
.ad-container .table-wrapper .el-table .el-loading-mask {
      z-index: 100;
}
.ad-container .table-wrapper .nodata .el-table__fixed {
      height: 47px !important;
}
.ad-container .table-wrapper .el-table--border .el-table__body-wrapper .el-table__row.el-table__row--striped {
      -webkit-box-shadow: none !important;
              box-shadow: none !important;
}
.ad-container .table-wrapper .el-table__body {
      overflow: hidden;
}
.ad-container .table-wrapper .link-go {
      font-size: 13px;
      color: #4A90E2;
      text-decoration: underline;
      font-family: PingFangSC-Regular;
}
.ad-container .table-wrapper td[class^=el-table_1_column] a {
      text-decoration: none;
}
.ad-container .table-wrapper td[class^=el-table_1_column] a:last-child {
      margin-right: 0px !important;
}
.ad-container .table-wrapper td[class^=el-table_1_column] a:first-child {
      margin-left: 0px !important;
}
.ad-container .previewDialog .preview-content .content-line {
    position: relative;
    /*width: 549px;*/
    /*height: 46px;*/
    font-family: PingFangSC-Semibold;
    font-size: 13px;
    color: #3A3A3A;
    line-height: 46px;
}
.ad-container .previewDialog .preview-content .content-line .left {
      width: 90px;
      height: 100%;
      padding-left: 18px;
      padding-right: 18px;
      text-align: left;
      position: absolute;
      left: 0;
      background: #F9F9F9;
      -webkit-box-shadow: inset 0 0px 0 0 #E8E8E8, inset 1px 0 0 0 #E8E8E8, inset 0 -1px 0 0 #E8E8E8, inset -1px 0 0 0 #E8E8E8;
              box-shadow: inset 0 0px 0 0 #E8E8E8, inset 1px 0 0 0 #E8E8E8, inset 0 -1px 0 0 #E8E8E8, inset -1px 0 0 0 #E8E8E8;
}
.ad-container .previewDialog .preview-content .content-line .right {
      width: 100%;
      height: 100%;
      padding-left: 108px;
      padding-right: 20px;
      -webkit-box-shadow: inset 0 0px 0 0 #E8E8E8, inset 0 -1px 0 0 #E8E8E8, inset -1px 0 0 0 #E8E8E8;
              box-shadow: inset 0 0px 0 0 #E8E8E8, inset 0 -1px 0 0 #E8E8E8, inset -1px 0 0 0 #E8E8E8;
}
.ad-container .previewDialog .preview-content .content-line .multi-line {
      padding-top: 12px;
      padding-bottom: 12px;
      line-height: 22px;
}
.ad-container .previewDialog .preview-content .content-line.zs_line {
    margin-bottom: 0px !important;
}
.ad-container .previewDialog .preview-content .content-line:nth-child(3),
  .ad-container .previewDialog .preview-content .content-line:nth-child(6) {
    margin-bottom: 10px;
}
.ad-container .previewDialog .preview-content .content-line:last-child {
    margin-bottom: 10px;
}
.ad-container .previewDialog .preview-content .content-line.keyword-line:nth-child(3) {
    margin-bottom: 0px;
}
.ad-container .previewDialog .preview-content .content-line.plan-count {
    margin-bottom: 0px;
}
.ad-container .add-total-dialog {
    padding: 0px;
    width: 418px;
    height: 176px;
}
.ad-container .add-total-dialog .el-dialog__body {
      font-family: PingFangSC-Regular;
      padding: 0px;
}
.ad-container .add-total-dialog .el-dialog__body .head-content {
        padding: 30px;
        width: 100%;
}
.ad-container .add-total-dialog .el-dialog__body .head-content .task-name {
          width: 312px;
          font-family: PingFangSC;
          font-size: 16px;
          line-height: 1.0;
          color: #3a3a3a;
          height: 16px;
          overflow: hidden;
          white-space: nowrap;
          -o-text-overflow: ellipsis;
             text-overflow: ellipsis;
}
.ad-container .add-total-dialog .el-dialog__body .el-form {
        -webkit-box-shadow: 0 -1px 0 0 rgba(153, 153, 153, 0.14);
                box-shadow: 0 -1px 0 0 rgba(153, 153, 153, 0.14);
        padding: 30px;
        height: 100px;
}
.ad-container .add-total-dialog .el-dialog__body .el-form .el-form-item {
          margin-bottom: 25px;
}
.ad-container .add-total-dialog .el-dialog__body .el-form .el-form-item .el-form-item__label {
            padding: 0px 0px 10px 0px;
            font-size: 13px;
            line-height: 18px;
            color: #3A3A3A;
}
.ad-container .add-total-dialog .el-dialog__body .el-form .el-form-item .el-form-item__content {
            width: 140px;
}
.ad-container .add-total-dialog .el-dialog__body .footer {
        width: 148px;
        font-size: 0px;
        margin-top: 0px;
        position: absolute;
        bottom: 30px;
        right: 19px;
}
.ad-container .add-total-dialog .el-dialog__body .footer .goon-btn {
          margin-left: 10px;
}
.ad-container .previewDialog .el-dialog {
    width: 650px;
    padding: 30px;
}
.ad-container .previewDialog .el-dialog .el-dialog__header .el-dialog__title {
      line-height: 22px;
}
.ad-container .previewDialog .el-dialog .el-dialog__header .el-dialog__headerbtn .el-dialog__close {
      color: #B5B5B5;
      font-size: 14px;
      position: absolute;
      top: 30px;
      right: 30px;
}
.ad-container .previewDialog .el-dialog .el-dialog__body {
      padding: 25px 0px 0px 0px;
}



