
.bind-tel-dialog .el-dialog {
  padding: 0;
  width: 310px;
  height: 312px;
}
.bind-tel-dialog .el-dialog .el-dialog__body {
    padding: 30px 0 10px 0;
    margin-top: 32px;
    -webkit-box-shadow: 0 -1px 0 0 rgba(153, 153, 153, 0.14);
            box-shadow: 0 -1px 0 0 rgba(153, 153, 153, 0.14);
}
.bind-tel-dialog .el-dialog__title {
  display: inline-block;
  height: 16px;
  margin: 3px 0 0 5px;
}
.bind-tel-dialog .el-dialog__headerbtn {
  font-size: 14px;
  margin-right: -11px;
}
.bind-tel-dialog .el-form {
