/* information */
.information {
  position: relative;
  width: 100%;
}
.information .info {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: top;
    -webkit-align-items: top;
        -ms-flex-align: top;
            align-items: top;
    margin-top: 38px;
}
.information .info dt.left-wrap {
      width: 92px;
      height: 92px;
      border-radius: 6px;
      border: solid 0.5px rgba(58, 58, 58, 0.2);
      overflow: hidden;
}
.information .info dt.left-wrap img {
        width: 92px;
        height: 92px;
}
.information .info .middle-wrap {
      -webkit-box-flex: 1;
      -webkit-flex: 1;
          -ms-flex: 1;
              flex: 1;
      margin-right: 395px;
}
.information .info p, .information .info dd {
      margin: 0;
      padding: 0;
}
.information .info dd {
      margin-left: 20px;
}
.information .info dd p.title {
        width: 100%;
        height: 28px;
        line-height: 28px;
        font-family: PingFangSC-Semibold;
        font-size: 20px;
        font-weight: 600;
        color: #3a3a3a;
}
.information .info dd p.sub-title {
        margin-top: 10px;
        font-size: 13px;
        line-height: 1.08;
        color: #888888;
}
.information .right {
    position: absolute;
    right: 0;
    top: 0px;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    line-height: 44px;
    font-size: 13px;
    color: #3a3a3a;
    border-top: 1px solid #e8e8e8;
    border-left: 1px solid #e8e8e8;
}
.information .right dl {
      margin: 0;
}
.information .right dl dt {
        padding: 0 18px;
        background-color: #f9f9f9;
        border-right: 1px solid #e8e8e8;
        border-bottom: 1px solid #e8e8e8;
}
.information .right dl dd {
        margin: 0;
        padding: 0 18px;
        border-right: 1px solid #e8e8e8;
        border-bottom: 1px solid #e8e8e8;
}



