.circle {
  position: fixed;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background-color: #f5a623;
  z-index: 99999;
  opacity: 0;
}
.moveCircle {
  -webkit-animation: mymove .6s linear;
          animation: mymove .6s linear;
}
@-webkit-keyframes mymove {
0% {
    opacity: .5;
}
40% {
    width: 26px;
    height: 26px;
}
100% {
    width: 10px;
    height: 10px;
    left: 95%;
    top: 95%;
    opacity: 1;
}
}
@keyframes mymove {
0% {
    opacity: .5;
}
40% {
    width: 26px;
    height: 26px;
}
100% {
    width: 10px;
    height: 10px;
    left: 95%;
    top: 95%;
    opacity: 1;
}
}



