
#page {
  width: 100%;
  height: 100%;
}
#page #page-content {
    width: 100%;
    height: 100%;
    padding-top: 200px;
    background: #3C4F64;
}
#page #page-content .form-wrapper {
      background: #3C4F64;
      position: absolute;
      width: 340px;
      top: 50%;
      left: 50%;
      margin-top: 50%;
      margin-left: 50%;
}
#page #page-content .form-wrapper.form-wrapper-login {
