}
#page #page-content .form-wrapper .el-row .el-col .grid-content .login-form .qk-form-item input:-webkit-autofill {
              -webkit-box-shadow: 0 0 0 1000px white inset;
}
#page #page-content .form-wrapper .el-row .el-col .grid-content .login-form .qk-form-item .el-form-item__content .el-checkbox {
              float: left;
}
#page #page-content .form-wrapper .el-row .el-col .grid-content .login-form .qk-form-item .el-form-item__content p {
              float: right;
