
.container {
  width: 100%;
  min-width: 1020px;
  padding: 32px 35px 150px 35px;
}
.container .nav {
    padding: 18px 0;
    height: 58px;
}
.container .nav .title {
      font-family: PingFangSC-Light;
      font-size: 16px;
      line-height: 22px;
      color: #888888;
}
.container .cards {
    padding: 29px 0 10px 0;
}
.container .cards .qk-dash-card {
      padding: 18px 20px;
}
.container .cards .qk-dash-card .title {
        font-size: 13px;
        line-height: 18px;
        margin-bottom: 8px;
}
.container .cards .qk-dash-card .highlight {
        margin-bottom: 0;
}
.container .cards .el-col.el-col-6 {
      padding-right: 10px;
}
.container .cards .el-col.el-col-6:last-of-type {
        padding-right: 0px;
}
.container .report-menus {
    padding: 30px 0;
    height: 100px;
}
.container .report-menus .select-report-app {
      float: left;
      margin-right: 10px;
}
.container .report-menus .select-report-type {
      margin-right: 10px;
}
.container .report-menus .select-report-type {
      width: 180px;
}
.container .chart-wrapper {
    height: 440px;
    margin-bottom: 51px;
    padding-top: 10px;
}
.container .chart-wrapper .rpt-chart {
      width: 100%;
      height: 100%;
}
.container .chart-wrapper #table-chart {
      width: 100%;
      height: 100%;
}
.container .input-wrapper {
    display: inline-block;
    float: left;
    margin-right: 10px;
}
.container .input-wrapper .el-select, .container .input-wrapper .el-form, .container .input-wrapper .el-button {
      display: inline-block;
}
.select-dropdown-home .el-select-dropdown__item span {
  color: #3a3a3a;
}



