
.deleDialog {
  width: 390px;
}
.deleDialog .el-dialog__body {
    padding-left: 45px;
    padding-top: 35px;
    position: relative;
}
.deleDialog .el-dialog__body .logo-waring {
      position: absolute;
      left: 0px;
      top: 30px;
}
.deleDialog .el-dialog__body .qk-title {
      color: #3A3A3A;
      font-family: PingFangSC-Regular;
      font-size: 14px;
      line-height: 21px;
}
.deleDialog .el-dialog__footer {
    padding-top: 13px;
}
.deleDialog .el-dialog__footer .dialog-footer {
      font-size: 0px;
}
.deleDialog .el-dialog__footer .dialog-footer .el-button {
        width: 70px;
}
.deleDialog .el-dialog__footer .dialog-footer .el-button:last-child {
        margin-left: 8px;
}



