
.main {
  width: 100%;
  padding-top: 60px;
  overflow-x: hidden;
}
.main .head {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    min-width: 1000px;
    height: 60px;
    z-index: 30000;
}
.main .head .top-nav .el-menu-item, .main .head .top-nav .el-submenu {
      font-family: PingFangSC-Regular;
}
.main .head .top-nav .el-menu-item span, .main .head .top-nav .el-submenu span {
        font-family: PingFangSC-Regular;
}
.main .head .top-nav .layout-account {
      width: auto;
      max-width: 267px;
      padding: 0;
}
.main .head .top-nav .layout-account .el-submenu__title {
        padding: 0 20px;
}
.main .head .top-nav .layout-account:hover .el-submenu__title {
        background-color: rgba(58, 58, 58, 0.3) !important;
}
.main .head .top-nav .layout-charge {
      text-decoration: underline;
}
.main .head .top-nav .layout-charge:hover {
        background-color: rgba(58, 58, 58, 0.3) !important;
}
.main .head .top-nav .underline {
      text-decoration: underline;
}
.main .head .top-nav .logo {
      width: 180px;
      padding-left: 80px;
}
.main .content {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}
.main .content .sidebar {
      position: fixed;
      bottom: 0;
      top: 60px;
      z-index: 888;
}
.main .content .sidebar + div {
      padding-left: 215px;
}



