
.finance-container {
  width: 100%;
  height: 100%;
  min-width: 1000px;
  padding: 50px 35px 150px 35px;
}
.finance-container .button-wrapper {
    margin-bottom: 30px;
}
.finance-container .button-wrapper button:nth-child(2) {
      float: right;
}
.finance-container .button-wrapper button:nth-child(3) {
      float: right;
}
.finance-container .title {
    margin-top: 0px;
    margin-bottom: 47px;
    font-size: 16px;
    font-family: PingFangSC-Light;
    line-height: 22px;
    color: #888888;
}
.finance-container .el-pagination {
    margin-bottom: 20px;
}
.finance-container .table-wrapper {
    width: 1050px;
    margin-bottom: 20px;
}
.finance-container .table-wrapper .nodata .el-table__fixed {
      height: 47px !important;
}
.finance-container .table-wrapper .el-table__body {
      overflow: hidden;
}
.finance-container .custom-btn {
    padding: 0;
}
.finance-container .el-table__fixed-right .cell {
    text-align: center;
}
.finance-container .el-table__fixed-right .cell div.opera {
      font-size: 13px;
      color: #4A90E2;
      letter-spacing: 0;
      text-align: center;
}
.finance-container .el-table__row {
    height: 46px;
}
.finance-container .fs13-c42 {
    font-size: 13px;
    color: #4A90E2;
    text-align: center;
}
.finance-container .custom-column {
    font-size: 0px;
}
.finance-container .link-go {
    cursor: pointer;
    font-size: 13px;
    color: #4A90E2;
    text-decoration: underline;
    font-family: PingFangSC-Regular;
}
.finance-container td[class^=el-table_1_column] .slider-wrap {
    opacity: 1;
    position: absolute;
    min-width: 106px;
    height: 46px;
    left: -43px;
    top: 0px;
    overflow: hidden;
    padding-left: 18px;
    background: #FCFCFC;
    -webkit-box-shadow: inset 1px 0 0 0 #E8E8E8, inset 0 -1px 0 0 #E8E8E8, inset -1px 0 0 0 #E8E8E8;
            box-shadow: inset 1px 0 0 0 #E8E8E8, inset 0 -1px 0 0 #E8E8E8, inset -1px 0 0 0 #E8E8E8;
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    -o-transition: transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
    -webkit-transform: translateX(106px);
        -ms-transform: translateX(106px);
            transform: translateX(106px);
}
.finance-container td[class^=el-table_1_column] .slider-wrap .el-button {
      height: 46px;
}
.finance-container td[class^=el-table_1_column]:hover .slider-wrap {
    opacity: 1;
    width: 106px;
    -webkit-transform: translateX(1px);
        -ms-transform: translateX(1px);
            transform: translateX(1px);
}
.finance-container td[class^=el-table_1_column] .three-dot {
    font-size: 13px;
    color: #4A90E2;
    letter-spacing: 0;
    text-align: center;
}
.finance-container .el-dialog {
    width: 390px;
    padding: 30px;
}
.finance-container .el-dialog .el-dialog__header .el-dialog__title {
      line-height: 22px;
}
.finance-container .el-dialog .el-dialog__footer {
      margin-top: -17px;
}
.finance-container .charge-success-dialog .el-dialog__body {
    padding-top: 0px;
}
.finance-container .charge-success-dialog .el-dialog__body .logo {
      top: 0px;
}
.finance-container .charge-success-dialog .el-dialog__body .qk-title {
      position: relative;
      top: 7px;
      font-size: 16px;
}
.finance-container .charge-success-dialog .el-dialog__footer {
    padding-top: 30px;
}



