
.invoice-container {
  padding: 50px 35px 150px 35px;
  width: 100%;
  height: 100%;
  min-width: 1000px;
}
.invoice-container .breadcrumb {
    height: 22px;
    margin-bottom: 47px;
}
.invoice-container .breadcrumb .breadcrumb-item {
      display: inline-block;
      float: left;
      margin-right: 10px;
      cursor: pointer;
}
.invoice-container .breadcrumb .breadcrumb-item .breadcrumb-item-inner {
        font-family: PingFangSC-Light;
        font-size: 16px;
        line-height: 22px;
        color: #888888;
}
.invoice-container .breadcrumb .breadcrumb-item .breadcrumb-separator {
        width: 6px;
        height: 12px;
        margin-left: 2px;
        display: inline-block;
        background-image: url("//qianka.b0.upaiyun.com/images/a688c7dd7a765df07ec7d9cfab76b68f.png");
        background-size: 6px 12px;
        background-position: center;
        background-repeat: no-repeat;
}
.invoice-container .breadcrumb .breadcrumb-item:last-child {
      cursor: text;
}
.invoice-container .table-wrapper .link-go {
    cursor: pointer;
    font-size: 13px;
    color: #4A90E2;
    text-decoration: underline;
    font-family: PingFangSC-Regular;
    margin-right: 12px;
}
.invoice-container .previewDialog .preview-content .content-line {
    position: relative;
    /*width: 549px;*/
    height: 46px;
    font-family: PingFangSC-Semibold;
    font-size: 13px;
    color: #3A3A3A;
    line-height: 46px;
}
.invoice-container .previewDialog .preview-content .content-line .left {
      width: 122px;
      height: 100%;
      padding-left: 18px;
      text-align: left;
      position: absolute;
      left: 0;
      background: #F9F9F9;
      -webkit-box-shadow: inset 0 0px 0 0 #E8E8E8, inset 1px 0 0 0 #E8E8E8, inset 0 -1px 0 0 #E8E8E8, inset -1px 0 0 0 #E8E8E8;
              box-shadow: inset 0 0px 0 0 #E8E8E8, inset 1px 0 0 0 #E8E8E8, inset 0 -1px 0 0 #E8E8E8, inset -1px 0 0 0 #E8E8E8;
}
.invoice-container .previewDialog .preview-content .content-line .left.left-long {
        padding-right: 18px;
        padding-top: 14px;
        line-height: 18px;
}
.invoice-container .previewDialog .preview-content .content-line .right {
      width: 100%;
      height: 100%;
      padding-left: 140px;
      padding-right: 20px;
      -webkit-box-shadow: inset 0 0px 0 0 #E8E8E8, inset 0 -1px 0 0 #E8E8E8, inset -1px 0 0 0 #E8E8E8;
              box-shadow: inset 0 0px 0 0 #E8E8E8, inset 0 -1px 0 0 #E8E8E8, inset -1px 0 0 0 #E8E8E8;
}
.invoice-container .previewDialog .preview-content .content-line .right.right-long {
        height: 67px;
        line-height: 67px;
}
.invoice-container .previewDialog .preview-content .content-line.zs_line:first-child {
    border-top: 1px solid #E8E8E8;
}
.invoice-container .previewDialog .preview-content .content-line.zs_line {
    margin-bottom: 0px !important;
}
.invoice-container .previewDialog .preview-content .content-line:nth-child(5),
  .invoice-container .previewDialog .preview-content .content-line:nth-child(8) {
    margin-bottom: 10px;
}
.invoice-container .previewDialog .preview-content .content-line:last-child {
    margin-bottom: 10px;
}
.invoice-container .previewDialog .preview-content .content-line.keyword-line:nth-child(3) {
    margin-bottom: 0px;
}
.invoice-container .previewDialog .preview-content .content-line.plan-count {
    margin-bottom: 0px;
}
.invoice-container .previewDialog .el-dialog {
    width: 650px;
    padding: 30px;
}
.invoice-container .previewDialog .el-dialog .el-dialog__header .el-dialog__headerbtn .el-dialog__close {
      color: #B5B5B5;
      font-size: 14px;
      position: absolute;
      top: 20px;
      right: 20px;
}
.invoice-container .previewDialog .el-dialog .el-dialog__body {
      padding: 25px 0px 0px 0px;
}
.invoice-container .el-dialog {
    width: 390px;
    padding: 30px;
}
.invoice-container .el-dialog .el-dialog__header .el-dialog__title {
      line-height: 22px;
}
.invoice-container .el-dialog .el-dialog__footer {
      margin-top: -12px;
}



